.headerBox {
  height: 100vh;

  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.headerTitleSec {
  position: relative;
  background-color: #e7e9eb;
  margin: 5px;
}
.headerTitleSec .floatMid {
  /* position: absolute; */
  margin-top: 2em;
  text-align: center;
}
.header-title {
  font-weight: bold;
}
.header-name {
  color: #011d3a;
}
.headerdescSec {
  margin-top: 2em;
}
.header-desc {
  text-align: justify;
  padding: 10px 15px;
  line-height: 1.3rem;
}

.sidePanelSec {
  height: 98vh;
  overflow: scroll;
}
.projectBox {
  margin-top: 2em;
}
.projectCards {
  padding: 10px;
  text-align: justify;
}

